@font-face {
    font-family: ELDA MARIA;
    src: url(./ELDA-MARIA.TTF);
}

.navbar {
    font-family: ELDA MARIA;
    height: 64px;
    background-color: #e53935;
}

.navbar .brand-logo {
    font-size: 35px;
    letter-spacing: 4px;
    text-shadow: 1px 1px 1px black;
    line-height: 78px;
}

.navbar i {
    text-shadow: 1px 1px 1px black;
}

.navbar .links a {
    font-size: 30px !important;
    line-height: 78px;
    text-shadow: 1px 1px 1px black;
}

.navbar .sidenav a {
    font-size: 30px !important;
    letter-spacing: 4px;
}