.doces .galeria {
    display: flex;
    /* flex-wrap: wrap; */
}

.doces .galeria .column {
    width: 50%;
    padding: 0px 10px;

}

/* wrapper config */
.doces .galeria .galeria-item {
    margin-top: 20px;
    font-size: 0;
    position: relative;
    overflow: hidden;
    width: 100%;
    border: 12px solid white;
}

/* img config */
.doces .galeria .galeria-item img {
    width: 100%;
    transition: all .2s;
}

/* img hover zoom */
.doces .galeria .galeria-item:hover img {
    transition: all .7s;
    transform: scale(1.1);
    filter: brightness(1.1);
}

/* texto */
.doces .galeria .galeria-item .galeria-texto {
    opacity: 0;
    padding: 5px 10px;
    font-size: 16px;
    position: absolute;
    bottom: 0px;
    width: 100%;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.671) 0%, transparent 100%);
    color: white;
    transition: all .3s;
}

.doces .galeria .galeria-item:hover .galeria-texto {
    opacity: 1;
    transition: all .7s;
}