.navbar {
    background-color:rgba(0, 0, 0, 0.418);
    z-index: 998;
    position: absolute; 
}

.main-banner {
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.main-banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.main-banner .mask {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    background-color: rgba(0, 0, 0, 0.205);
}

.main-banner .texto {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    display: flex;
    color: white;
    padding: 0px 25px;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    text-align: center;
}

.main-banner .texto h2 {
    width: 100%;
    text-shadow: 1px 1px 1px black;
}

.main-banner .texto p {
    text-shadow: 1px 1px 1px black;

}
